<template>
  <v-card :loading="loading" class="mx-auto my-12" v-if="movie">
    <template slot="progress">
      <v-progress-linear color="deep-purple" height="10" indeterminate></v-progress-linear>
    </template>

    <v-img height="400" :src="movie.Poster"></v-img>

    <v-card-title>{{ movie.Title }}</v-card-title>

    <v-card-subtitle> {{ movie.Year }} </v-card-subtitle>

    <v-card-text>
      <v-row align="center" class="mx-0">
        <v-rating :value="parseFloat(movie.imdbRating)" :length="10" color="amber" half-increment dense half-increments readonly size="14"></v-rating>

        <div class="grey--text ms-4">{{ movie.imdbRating }}/10 ({{ movie.imdbVotes }} votes)</div>
        <div class="text-center">
          <v-dialog v-model="dialog" width="500">
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="red lighten-2" dark v-bind="attrs" v-on="on">
                View Rating
              </v-btn>
            </template>

            <v-card>
              <v-card-title class="text-h5 grey lighten-2">
                Rating and Review
              </v-card-title>

              <v-card-text>
                <!-- <v-card v-for="review in moview.Ratings" :key="review.Source" class="p-3">
                  <h6>
                    {{ review.Source }}
                  </h6>
                  <p>
                    {{ review.Value }}
                  </p>
                </v-card> -->
              </v-card-text>

              <v-divider></v-divider>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" text @click="dialog = false">
                  Close
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </div>
      </v-row>

      <div class="my-4 text-subtitle-1">Cast: {{ movie.Actors }}</div>

      <div>{{ movie.Plot }}</div>
    </v-card-text>

    <v-divider class="mx-4"></v-divider>

    <v-card-title>Genre</v-card-title>

    <v-card-text>
      <v-chip-group active-class="purple accent-4 white--text" column>
        <v-chip v-for="genre in genres" :key="genre">
          {{ genre }}
        </v-chip>
      </v-chip-group>
    </v-card-text>

    <v-card-actions>
      <v-btn color="deep-purple lighten-2" text @click="back">
        Back
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
// import Header from '../components/Header.vue';
// @ is an alias to /src

export default {
  // components: { Header },
  name: 'Detail',
  data() {
    return {
      movie: {},
      genres: [],
      loading: false,
      rating: 0,
      dialog: false,
    };
  },
  methods: {
    reserve() {
      this.loading = true;

      setTimeout(() => (this.loading = false), 2000);
    },
    back() {
      this.$router.push('/');
    },
  },
  mounted() {
    let imdbId = this.$route.params.imdbId;
    console.log(`Movie Id: ${imdbId}`);
    fetch(`https://www.omdbapi.com/?i=${imdbId}&apikey=87d10179`)
      .then((response) => response.json())
      .then((data) => {
        this.movie = data;
        this.genres = data.Genre.split(', ');
        this.rating = parseFloat(data.imdbRating);
      });
  },
};
</script>
