<template>
  <v-row>
    <v-col cols="8">
      <v-text-field label="Enter movie name" v-model="searchedMovie"></v-text-field>
    </v-col>
    <v-col cols="4">
      <v-btn elevation="2" color="purple" dark block v-on:click="searchPressed">Search</v-btn>
    </v-col>
  </v-row>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'Search',
  data() {
    return {
      searchedMovie: '',
    };
  },
  methods: {
    searchPressed: function() {
      this.$emit('searchApi', this.searchedMovie);
    },
  },
};
</script>
